import ReactDOM from 'react-dom';
import './style.css';
import { useState, useEffect, useRef } from 'react';

import logo from './data/assets/shortcut-script-app.svg';
import exmp from './data/assets/home.jpg';

function Main(props) {

    let [pages, setPages] = useState(['home', 'about', 'blog', 'service', 'contact']);
    let [page, setPage] = useState(pages[3]);

    document.title = 'Fuong';

    useEffect(() => {
        window.addEventListener('wheel', wheelEvent);
        return () => {
            window.removeEventListener('wheel', wheelEvent);
        }
    })


    function wheelEvent(e) {
        e.preventDefault();
        let index = pages[pages.indexOf(page) + (e.deltaY < 0 ? -1 : 1)];
        if (index) {
            pageTo(index);
        };
    }

    function pageTo(name) {
        setPage(name);
    }

    let btnRender = pages.map((value, index) => {
        return (
            <HeaderBtn className={value === page ? 'header-btn on-page' : 'header-btn'} key={index} index={index} value={value} onClick={() => { pageTo(value) }}></HeaderBtn>
        )
    })

    return (
        <div id='main'>
            <Header  >
                {btnRender}
            </Header>
            <Page className={pages[0]} page={page}>
                <HomePage discover={() => { pageTo(pages[2]) }} />
            </Page>
            <Page className={pages[1]} page={page}></Page>
            <Page className={pages[2]} page={page}>
                <BlogPage>
                    <WriteGrid></WriteGrid>
                </BlogPage>
            </Page>
            <Page className={pages[3]} page={page}>
                <div className='service-page'>
                    <div className='service-grid'>
                        <ServiceItem page={page} delay={200} title="web design" img="web-design.svg" />
                        <ServiceItem page={page} delay={300} title="development" img="setting.svg" />
                        <ServiceItem page={page} delay={400} title="marketing" img="bullhorn.svg" />
                        <ServiceItem page={page} delay={500} title="strategy" img="analysis.svg" />
                        <ServiceItem page={page} delay={600} title="support" img="customer-service (1).svg" />
                        <ServiceItem page={page} delay={700} title="experience" img="certificate.svg" />
                    </div>
                </div>
            </Page>
            <Page className={pages[4]} page={page}></Page>
        </div>
    )
}

function HomePage(props) {
    return (
        <div className='home-page'>
            <div className='btn-container'>
                <a onClick={props.discover} className='btn btn3'>discover!</a>
            </div>
        </div>
    )
}

function ServiceItem(props) {
    let className = props.page == 'service' ? 'service-item service-anim' : 'service-item';

    let [display, setDisplay] = useState('hidden');

    const imgRef = useRef(null);

    let img = <object data={`img/${props.img}`} style={{ visibility: display }} type="image/svg+xml" ref={imgRef} onLoad={() => {
        setDisplay('visible');
        let paths = imgRef.current.contentDocument.querySelectorAll('path');
        paths.forEach((item, index) => {
            item.style.strokeDasharray = item.getTotalLength();
            item.style.fill = 'transparent';
            item.style.strokeWidth = '1px';
            item.animate([
                {
                    strokeDashoffset: item.getTotalLength(),
                    fill: 'transparent',
                    stroke: '#333',
                },
                {
                    strokeDashoffset: 0,
                    stroke: '#333'

                },
                {
                    fill: '#333',
                    stroke: '#333'
                }
            ], {
                duration: 3000 + props.delay,
                fill: 'forwards',
                delay: props.delay
            });
        })
    }} />;

    return (
        <div className={className} style={{ animationDelay: `${props.delay}ms` }}>
            {props.page == 'service' ? img : <div></div>}
            <span className={props.page == 'service' ? 'service-title title-appear' : 'service-title'}>{props.title}</span>
        </div>
    )
}



function Write(props) {
    return (
        <div onClick={props.onClick} className={props.className == 'expand' ? 'write expand' : 'write'} style={{ backgroundImage: `url(${props.src})` }}>
            <div className='write-des-container' >
                <div className='write-time'>{props.time}</div>
                <div className='write-title'>{props.title}</div>
                <div className='write-descript'>{props.descript}</div>
            </div>
        </div >
    )
}

function WriteGrid(props) {
    let [expand, setExpand] = useState([true, false, false, false, false]);

    function expandWrite(index) {
        let cExpand = Array(expand.length).fill(false);
        cExpand[index] = true;
        setExpand(cExpand);
    }

    let data = [
        {
            title: 'coding with react',
            descript: `
                React JS is a JavaScript library used in web development to build 
                interactive elements on websites. But if you’re not familiar with 
                JavaScript or JavaScript libraries, that’s not a helpful definition. 
                So let’s take a step back and deal with those terms first.
            `,
            img: '/img/0.jpg',
            time: new Date()
        },
        {
            title: 'coding with javascript',
            descript: `
                Anytime you visit a website where things move, refresh, or otherwise 
                change on your screen without requiring you to manually reload a web 
                page, there’s a very good chance JS is the language making it happen.
            `,
            img: '/img/1.jpg',
            time: new Date()
        },
        {
            title: 'coding with css',
            descript: `
                Cascading Style Sheets (CSS) is a style sheet language used 
                for describing the presentation of a document written in a markup 
                language such as HTML. CSS is a cornerstone technology of the World Wide Web
                , alongside HTML and JavaScript.
            `,
            img: '/img/2.jpg',
            time: new Date()
        },
        {
            title: 'coding with html',
            descript: `
                Hypertext Markup Language, a standardized system for 
                tagging text files to achieve font, color, graphic, 
                and hyperlink effects on World Wide Web pages.
            `,
            img: '/img/3.jpg',
            time: new Date()
        },
        {
            title: 'coding with css',
            descript: 'Phuong',
            img: '/img/4.jpg',
            time: new Date()
        }
    ]

    let writeRender = expand.map((value, index) => {
        return (
            <Write time={data[index].time.toLocaleDateString()} src={data[index].img} descript={data[index].descript} title={data[index].title} key={index} onClick={() => { expandWrite(index) }} className={expand[index] ? 'expand' : 'norm'}></Write>
        )
    })

    return (
        <div>
            <div className='blog-header'>
                <div>Recently posts</div>
                <div>Visit my blog</div>
            </div>
            <div className='write-grid' id='blogGrid'>
                {writeRender}
            </div>
        </div>
    )
}

function BlogPage(props) {
    return (
        <div className='blog-page'>
            {props.children}
        </div>
    )
}

function Page(props) {
    const fieldRef = useRef();

    useEffect(() => {
        if (props.page === props.className) {
            fieldRef.current.scrollIntoView();
        }
    })

    return (
        <div className={props.className} ref={fieldRef}>
            {props.children}
        </div>
    )
}

function Header(props) {

    let [sticky, setSticky] = useState(false);

    function scrollEvent(e) {
        if (window.scrollY > 100) {
            setSticky(true)
        } else {
            setSticky(false)
        }
    }

    useEffect(() => {
        document.addEventListener('scroll', scrollEvent);
        return () => {
            window.removeEventListener('scroll', scrollEvent);
        }
    })

    return (
        <div className={sticky ? 'header sticky' : 'header'} >
            <img src={logo} width='30px' height='30px' />
            <div className='paging'>
                {props.children}
            </div>
            <div className='login-btn'>
                <button>Login</button>
            </div>
        </div >
    )
}

function HeaderBtn(props) {
    function scrollToPage() {
        window.scroll(0, props.index * window.innerHeight);
    }

    return <div onClick={props.onClick} className={props.className}><span>{props.value}</span></div>
}

ReactDOM.render(<Main />, document.getElementById('root'));